import React, { Component, useState } from 'react';
import FormControl from '../../../../components/pages-component/form-controls/components/form-control';
import Form from '../../../../components/pages-component/form/form'
import Button from '../../../../components/pages-component/button/button'
import ErrorBox from '../../../../components/pages-component/form/components/error-box';
import Loader from '../../../../components/ui/loader/loader';
import * as utilityFunctions from '../../../../utility-functions/utility-functions'
import postJobEnquiry from '../../../../services/api/post-job-enquiry'
import Modal from '../../../../components/ui/modal/modal';
import FormSuccessMsg from '../../../../components/pages-component/form-success-msg/form-success-msg';

const ApplyForm = (props) => {

    const [name, setname] = useState('');
    const [email, setemail] = useState('');
    const [contact_number, setcontact_number] = useState('');
    const [resume, setresume] = useState('');
    const [message, setmessage] = useState('');
    const [errorMsg, seterrorMsg] = useState(null);
    const [formSubmissionStart, setformSubmissionStart] = useState(false);
    const [showModal, setshowModal] = useState(false);

    const handleInputChange = (e) => {
        e.target.name(e.target.value)
    }

    const submitCallBack = (data) => {
        if (data.status === 201) {
            clearForm()
            utilityFunctions.clearSelectField('.select-input');
            seterrorMsg(null)
            setformSubmissionStart(false)
            setshowModal(true)
        } else if (data.response) {
            seterrorMsg(data.response.data)
            setformSubmissionStart(false)
            setshowModal(false)
        } else {
            const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
            seterrorMsg(genericErrorMsg)
            setformSubmissionStart(false)
            setshowModal(false)
        }
    }

    const onSubmitHandle = (e) => {
        e.preventDefault()
        setformSubmissionStart(true)
        const data = formData()
        if (data) {
            postJobEnquiry(submitCallBack, data)
        }
    }

    const formData = () => {
        if (props.position) {
            const data = {
                name: name,
                email: email,
                contact_number: contact_number,
                resume: resume,
                message: message,
                position: props.position
            }
            return data
        }
    }

    const clearForm = () => {
        setname('')
        setemail('')
        setcontact_number('')
        setresume('')
        setmessage('')
    }

    const closeModal = () => {
        setshowModal(false)
        props.openForm()
    }

    return (
        <div>
            <Modal show={showModal} clicked={closeModal}>
                <FormSuccessMsg />
            </Modal>
            <Form classValue="form form--careers-generic-form" onSubmitHandler={onSubmitHandle}>
                <div className="form-controls three-col-layout">
                    <FormControl>
                        <label htmlFor="name">
                            Name
                                <span className="star">*</span>
                        </label>
                        <input
                            type="text"
                            name="name"
                            placeholder="Enter Name"
                            className="input-element"
                            id="name"
                            required={true}
                            value={name}
                            onChange={(e) => handleInputChange(e)}
                        />
                    </FormControl>
                    <FormControl>
                        <label htmlFor="email">
                            Email
                                <span className="star">*</span>
                        </label>
                        <input
                            type="email"
                            name="email"
                            placeholder="Enter Email"
                            className="input-element"
                            id="email"
                            required={true}
                            value={email}
                            onChange={(e) => handleInputChange(e)}
                        />
                    </FormControl>
                    <FormControl>
                        <label htmlFor="contact_number">
                            Contact Number
                                <span className="star">*</span>
                        </label>
                        <input
                            type="text"
                            name="contact_number"
                            placeholder="Contact No. with Country code (e.g. +91XXXXXXXXXX)"
                            className="input-element"
                            id="contact_number"
                            required={true}
                            value={contact_number}
                            onChange={(e) => handleInputChange(e)}
                        />
                    </FormControl>
                    <FormControl>
                        <label htmlFor="resume">
                            Resume
                                <span className="star">*</span>
                        </label>
                        <input
                            type="text"
                            name="resume"
                            placeholder="Enter Resume Link"
                            className="input-element"
                            id="resume"
                            required={true}
                            value={resume}
                            onChange={(e) => handleInputChange(e)}
                        />
                    </FormControl>
                    <FormControl>
                        <label htmlFor="message">
                            Message
                            <span className="star">*</span>
                        </label>
                        <textarea
                            type="textarea"
                            name="message"
                            placeholder="Leave Your Message..."
                            className="input-element"
                            id="message"
                            value={message}
                            onChange={(e) => handleInputChange(e)}
                        />
                    </FormControl>
                </div>
                {errorMsg ? <ErrorBox isRequired errorMsg={errorMsg} /> : null}
                {formSubmissionStart ? <Loader>Submitting your request...</Loader> : null}
                <Button
                    type="submit"
                    classValue="form-btn"
                    disabled={formSubmissionStart}
                >
                    Submit
                    </Button>
            </Form>
        </div>
    )

}

export default ApplyForm