import React, { Component, useEffect, useState } from 'react';
import { connect } from 'react-redux'
import PageBanner from '../../../../components/ui/page-banner/page-banner';
import SectionHeading from '../../../../components/pages-component/section-heading/section-heading';
import '../career/career.css'
import JobDescription from './job-description';
import { Link } from 'react-router-dom'
import getJOb from '../../../../services/api/fetch-job'
import * as actionType from '../../../../store/actions/action-type'
import ErrorPage from '../../../error-page/error-page';
import Header from '../../../../components/layout/header/header';
import Footer from '../../../../components/layout/footer/footer';

const Apply = (props) => {

    const { errMsg, seterrMsg } = useState('');
    const { open, setopen } = useState(false);

    useEffect(() => {
        const id = props.match.params.id
        if (id) {
            getJOb(callback, id)
        }
    },[])

    const callback = (data) => {
        if (data.status === 200) {
            props.getJob(data.data)
            seterrMsg('')
        } else {
            seterrMsg(data.status)
        }
    }

    const openForm = () => {
        setopen(!open)
    }

    return (
        <React.Fragment>
            <div className="page page--careers">
                <Header />
                <PageBanner heading={"Careers in Ethereal Machines"} subHeading={"Make your dream job come true"} classValue={"page-banner--careers"} />
                <section className="section section--openings">
                    {
                        errMsg === 'Not Found'
                            ?
                            <ErrorPage msg={"job"} />
                            :
                            <div className="container">
                                <SectionHeading classValue={'u-margin-bottom-big u-text-center'} name={props.jobItems.position} />
                                <JobDescription
                                    jobItem={props.jobItems}
                                    openForm={openForm}
                                    open={open}
                                />
                            </div>
                    }

                </section>
                <Footer />
            </div>
        </React.Fragment>
    )
}

const mapStateToProps = (state) => {
    return {
        jobItems: state.jobs.jobItemsById,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getJob: (data) => {
            dispatch({
                type: actionType.FETCH_JOB_BY_ID,
                value: data
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Apply)